"use client";
import { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import Image from "next/image";
import banner1 from "@/public/newimages/wheelbanner.png";

export default function Timestamp() {
  const [newUrl, setNewUrl] = useState("");
  const { t } = useTranslation();
  const [source, setSource] = useState("");

  const ONE_DAY_IN_MS = 1 * 24 * 60 * 60 * 1000; // –ö–æ–Ω—Å—Ç–∞–Ω—Ç–∞ –¥–ª—è –¥–≤—É—Ö —Å—É—Ç–æ–∫
  const ONE_MINUTE_IN_MS = 60 * 1000; // –ö–æ–Ω—Å—Ç–∞–Ω—Ç–∞ –¥–ª—è –æ–¥–Ω–æ–π –º–∏–Ω—É—Ç—ã
  const [redirectUrl, setRedirectUrl] = useState(""); // –°–æ—Å—Ç–æ—è–Ω–∏–µ –¥–ª—è URL –ø–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è
  // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è —Å–æ—Å—Ç–æ—è–Ω–∏–π
  const [stage, setStage] = useState(() => {
    // Ensure code runs only on client-side
    if (typeof window !== "undefined") {
      // –ü–æ–ª—É—á–∞–µ–º —Ç–µ–∫—É—â–µ–µ –∑–Ω–∞—á–µ–Ω–∏–µ 'stage' –∏–∑ localStorage –ø—Ä–∏ –ø–µ—Ä–≤–æ–π –∑–∞–≥—Ä—É–∑–∫–µ
      return localStorage.getItem("stage") || "first-stage";
    }
    return "first-stage"; // Default to 'first-stage' for SSR
  });

  const [timestamp, setTimestamp] = useState(() => {
    // Ensure code runs only on client-side
    if (typeof window !== "undefined") {
      // –ü–æ–ª—É—á–∞–µ–º —Ç–µ–∫—É—â–µ–µ –∑–Ω–∞—á–µ–Ω–∏–µ 'timestamp' –∏–∑ localStorage –ø—Ä–∏ –ø–µ—Ä–≤–æ–π –∑–∞–≥—Ä—É–∑–∫–µ
      return localStorage.getItem("timestamp") || null;
    }
    return null;
  });

  const [remainingTime, setRemainingTime] = useState(ONE_DAY_IN_MS); //

  // useEffect –¥–ª—è –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏–∏ —Å–æ—Å—Ç–æ—è–Ω–∏—è –Ω–∞ –∫–ª–∏–µ–Ω—Ç–µ
  useEffect(() => {
    if (typeof window !== "undefined") {
      const storedStage = localStorage.getItem("stage") || "first-stage";

      const storedTimestamp = localStorage.getItem("timestamp");

      setStage(storedStage);

      if (storedTimestamp) {
        const timeElapsed = Date.now() - parseInt(storedTimestamp, 10);
        setRemainingTime(ONE_DAY_IN_MS - timeElapsed);
        setTimestamp(parseInt(storedTimestamp, 10));
      } else {
        const newTimestamp = Date.now();
        localStorage.setItem("timestamp", newTimestamp.toString());
        setTimestamp(newTimestamp);
      }
    }
  }, []);

  // useEffect –¥–ª—è —É—Å—Ç–∞–Ω–æ–≤–∫–∏ URL –ø–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è –Ω–∞ –æ—Å–Ω–æ–≤–µ source
  useEffect(() => {
    if (typeof window !== "undefined") {
      let url = "";
      switch (source) {
        case "partner1039":
          url = `https://info.topbon.us/partner_aurnd/${newUrl}&creative_id=XXL_JIN`;
          break;
        case "partner1043":
          url = `https://info.topbon.us/rnd1043/${newUrl}&creative_id=XXL_JIN`;
          break;
        case "partner1044":
          url = `https://info.topbon.us/rnd1044/${newUrl}&creative_id=XXL_JIN`;
          break;
        case "CLD_VIP":
          url = `https://link.bo-nus.com/rnd_cld/${newUrl}&creative_id=XXL_JIN`;
          break;
        case "partner1045_b1":
          url = `https://link.bo-nus.com/rnd_cld/${newUrl}&creative_id=XXL_JIN`;
          break;
        default:
          url = `https://info.topbon.us/aurnd/${newUrl}&creative_id=XXL_JIN`;
      }
      setRedirectUrl(url); // –°–æ—Ö—Ä–∞–Ω—è–µ–º URL –≤ —Å–æ—Å—Ç–æ—è–Ω–∏–µ
    }
  }, [source]);
  console.log("RED", redirectUrl, source);

  // useEffect –¥–ª—è —Å–∏–Ω—Ö—Ä–æ–Ω–∏–∑–∞—Ü–∏–∏ stage —Å localStorage
  useEffect(() => {
    if (typeof window !== "undefined" && stage) {
      localStorage.setItem("stage", stage);
      console.log("STAGE", stage);
    }
  }, [stage]);

  // useEffect –¥–ª—è –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –æ—Å—Ç–∞–≤—à–µ–≥–æ—Å—è –≤—Ä–µ–º–µ–Ω–∏ –∏ –ø–µ—Ä–µ—Ö–æ–¥–∞ –Ω–∞ —Ç—Ä–µ—Ç–∏–π —ç—Ç–∞–ø
  useEffect(() => {
    if (
      typeof window !== "undefined" &&
      stage === "second-stage" &&
      timestamp
    ) {
      const intervalId = setInterval(() => {
        const timeElapsed = Date.now() - timestamp;
        const newRemainingTime = ONE_DAY_IN_MS - timeElapsed;
        setRemainingTime(newRemainingTime);

        if (newRemainingTime <= 0) {
          setStage("third-stage");
          localStorage.setItem("stage", "third-stage");
          clearInterval(intervalId);
        }
      }, 1000);

      return () => clearInterval(intervalId);
    }
  }, [stage, timestamp]);

  ///////////////////////////////////////////////
  const [customer, setCustomer] = useState();
  const [user, setUser] = useState();
  useEffect(() => {
    if (typeof window !== "undefined") {
      // –ò–∑–≤–ª–µ—á–µ–Ω–∏–µ —Å—Ç—Ä–æ–∫–∏ –∏–∑ localStorage
      const userData = localStorage.getItem("userData");

      // –ü—Ä–æ–≤–µ—Ä–∫–∞, —á—Ç–æ –¥–∞–Ω–Ω—ã–µ —Å—É—â–µ—Å—Ç–≤—É—é—Ç
      if (userData) {
        // –ü—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ —Å—Ç—Ä–æ–∫–∏ –≤ –æ–±—ä–µ–∫—Ç JSON
        const userObject = JSON.parse(userData);

        // –ò–∑–≤–ª–µ—á–µ–Ω–∏–µ –∑–Ω–∞—á–µ–Ω–∏—è –ø–æ–ª—è 'customer' –∏ 'id'
        const customerValue = userObject.customer;
        const idValue = userObject.id;

        // –£—Å—Ç–∞–Ω–æ–≤–∫–∞ —Å–æ—Å—Ç–æ—è–Ω–∏–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –∏ –∫–ª–∏–µ–Ω—Ç–∞
        setUser(idValue);
        setCustomer(customerValue);

        // –í—ã–≤–æ–¥ –∑–Ω–∞—á–µ–Ω–∏—è –ø–æ–ª—è 'customer'
        console.log("–ó–Ω–∞—á–µ–Ω–∏–µ –ø–æ–ª—è customer:", customerValue);
      } else {
        console.log("–î–∞–Ω–Ω—ã–µ userData –æ—Ç—Å—É—Ç—Å—Ç–≤—É—é—Ç –≤ localStorage.");
      }
    }
  }, []);
  const sendUserData = async (userId, customerType, nameEvent) => {
    try {
      const response = await fetch(
        "https://bonusnumber1.com/api/jin/jin1.php",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
          },
          body: new URLSearchParams({
            customerId: userId,
            customer: customerType,
            event: nameEvent,
          }),
        }
      );

      const result = await response.json();

      if (response.ok) {
        console.log("–î–∞–Ω–Ω—ã–µ —É—Å–ø–µ—à–Ω–æ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω—ã:", result);
      } else {
        console.error("–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–∫–µ –¥–∞–Ω–Ω—ã—Ö:", result);
      }
    } catch (error) {
      console.error("–û—à–∏–±–∫–∞ —Å–µ—Ç–∏:", error);
    }
  };
  ///////////////////////////////////////////////
  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –ø–µ—Ä–µ—Ö–æ–¥–∞ –Ω–∞ –≤—Ç–æ—Ä–æ–π —ç—Ç–∞–ø
  const scndstage = () => {
    setStage("second-stage");
    const newTimestamp = Date.now();
    setTimestamp(newTimestamp);
    localStorage.setItem("timestamp", newTimestamp.toString());

    // –û—Ç–ø—Ä–∞–≤–∫–∞ –¥–∞–Ω–Ω—ã—Ö –Ω–∞ —Å–µ—Ä–≤–µ—Ä
    if (user && customer) {
      sendUserData(user, customer, "FIRST EVENT");
    }
  };

  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è —Å–±—Ä–æ—Å–∞ —Å–æ—Å—Ç–æ—è–Ω–∏—è –Ω–∞ –ø–µ—Ä–≤—ã–π —ç—Ç–∞–ø –∏ –æ—Ç–∫—Ä—ã—Ç–∏—è –Ω–æ–≤–æ–π –≤–∫–ª–∞–¥–∫–∏
  const resetToFirstStage = () => {
    setStage("first-stage");
    localStorage.setItem("stage", "first-stage");
    localStorage.removeItem("timestamp"); // –£–¥–∞–ª—è–µ–º —Ç–∞–π–º—Å—Ç–∞–º–ø –∏–∑ localStorage

    if (redirectUrl) {
      // –û—Ç–∫—Ä—ã–≤–∞–µ–º –Ω–æ–≤—É—é –≤–∫–ª–∞–¥–∫—É —Å URL –∏–∑ —Å–æ—Å—Ç–æ—è–Ω–∏—è
      window.open(redirectUrl, "_blank");
      console.log("REDIRECT", redirectUrl);
    }
    // –û—Ç–ø—Ä–∞–≤–∫–∞ –¥–∞–Ω–Ω—ã—Ö –Ω–∞ —Å–µ—Ä–≤–µ—Ä
    if (user && customer) {
      sendUserData(user, customer, "SECOND EVENT");
    }
  };

  // –§–æ—Ä–º–∞—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ –æ—Å—Ç–∞–≤—à–µ–≥–æ—Å—è –≤—Ä–µ–º–µ–Ω–∏
  const formatTime = (milliseconds) => {
    const totalSeconds = Math.max(0, Math.floor(milliseconds / 1000));
    const hours = Math.floor(totalSeconds / 3600);
    const minutes = Math.floor((totalSeconds % 3600) / 60);
    const seconds = totalSeconds % 60;

    return `${hours.toString().padStart(2, "0")}:${minutes
      .toString()
      .padStart(2, "0")}:${seconds.toString().padStart(2, "0")}`;
  };

  return (
    <>
      {stage != null && (
        <div className="flex items-center justify-end jins w-full">
          {stage === "first-stage" && (
            <div className="frstjin">
              <h5 className="h5">
                {t("Click Here to Unleash Your Magic Bonus!")}
              </h5>
              <p className="p">{t("Get a bonus just for you tomorrow!")}</p>
              <Image loading="lazy" src={banner1} alt="banner1" />
              <div className="wrap-button">
                <button className="btn btn-mini" onClick={scndstage}>
                  <p>{t("Get Bonus")}</p>
                </button>
              </div>
            </div>
          )}
          {stage === "second-stage" && (
            <div className="scnjin">
              <h5 className="h5">
                {t("Thank you! Your Bonus Will Be Here Soon")}
              </h5>
              <p className="p">
                {t("Come back after")}{" "}
                <span className="goldie">{formatTime(remainingTime)}</span>{" "}
                {t("to collect it!")}
                <Image loading="lazy" src={banner1} alt="banner1" />
              </p>
            </div>
          )}
          {stage === "third-stage" && (
            <div className="thrdjin">
              <h5 className="h5">{t("Your Bonus is Ready!")}</h5>
              <p className="p">{t("Click below to claim your magical reward!")}</p>

              <Image loading="lazy" src={banner1} alt="banner1" />

              <button className="btn btn-new" onClick={resetToFirstStage}>
                <p> {t("Bonus Ready!")}</p>
              </button>
            </div>
          )}
        </div>
      )}
    </>
  );
}
